@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap);
:root {
  --body-font: "Poppins", sans-serif;
  --header-height: 3.25rem;

  --color-hue: 270;
  --color-primary: hsl(var(--color-hue), 70%, 60%);
  --color-primary-two: hsl(var(--color-hue), 55%, 55%);
  --color-primary-light: hsl(var(--color-hue), 90%, 85%);

  --color-title: hsl(var(--color-hue), 8%, 15%);
  --color-text: hsl(var(--color-hue), 8%, 45%);
  --color-text-light: hsl(var(--color-hue), 8%, 65%);

  --color-input: hsl(var(--color-hue), 70%, 95%);
  --color-body: hsl(var(--color-hue), 60%, 99%);
  --color-container: #fff;

  --font-size-0-75: 0.75rem;
  --font-size-1: 1rem;
  --font-size-1-25: 1.25rem;
  --font-size-1-5: 1.5rem;
  --font-size-2: 2rem;
  --font-size-2-5: 2.5rem;
  --font-size-3: 3rem;

  --margin-0-5: 0.5rem;
  --margin-0-75: 0.75rem;
  --margin-1: 1rem;
  --margin-1-25: 1.25rem;
  --margin-1-5: 1.5rem;
  --margin-2: 2rem;
  --margin-3: 3rem;

  --width-desktop: 1280px;
  --width-tablet: 768px;

  --z-index-1: 1;
  --z-index-10: 10;
  --z-index-100: 100;
}

* {
  box-sizing: border-box;
}

body {
  color: hsl(270, 8%, 45%);
  color: var(--color-text);
  background-color: hsl(270, 60%, 99%);
  background-color: var(--color-body);
  font-size: 1rem;
  font-size: var(--font-size-1);
  font-family: "Poppins", sans-serif;
  font-family: var(--body-font);
  margin: 3.25rem 0 0 0;
  margin: var(--header-height) 0 0 0;
}

body.dark-mode {
  --color-primary-two: hsl(var(--color-hue), 30%, 5%);
  --color-title: hsl(var(--color-hue), 8%, 95%);
  --color-text: hsl(var(--color-hue), 8%, 75%);
  --color-input: hsl(var(--color-hue), 30%, 15%);
  --color-body: hsl(var(--color-hue), 25%, 10%);
  --color-container: hsl(var(--color-hue), 30%, 15%);
}

h1,
h2,
h3 {
  color: hsl(270, 8%, 15%);
  color: var(--color-title);
  align-items: center;
  margin: 0;
}

p {
  margin: 0;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

html {
  scroll-behavior: smooth !important;
}

.color-dropdown {
  position: relative;
}

.color-dropdown-header {
  display: flex;
}

.color-dropdown-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 1rem;
  gap: 1rem;
  padding: 1rem;
  position: absolute;
  border-radius: 8px;
  box-shadow: 1px 1px 2px hsl(0deg 8% 40% / 50%);
  top: 2.125rem;
  left: -1rem;
  background-color: var(--color-body);
}

.color-dropdown-item {
  box-sizing: border-box;
  height: var(--font-size-1-25);
  width: var(--font-size-1-25);
  border-radius: 50%;
  margin-right: var(--margin-0-5);
  cursor: pointer;
}

.icon {
  height: 1.5rem;
  fill: var(--color-text);
  cursor: pointer;
}

.icon:hover {
  fill: var(--color-primary);
}

.header {
  width: 100%;
  position: fixed;
  height: var(--header-height);
  top: 0;
  left: 0;
  background-color: var(--color-body);
  box-shadow: 1px -5px 10px hsla(var(--color-hue), 8%, 40%, 0.5);
  z-index: var(--z-index-100);
}

.nav {
  font-weight: 600;
  color: var(--color-title);
  background-color: var(--color-body);
  max-width: var(--width-desktop);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 3rem;
  margin: auto;
  height: 100%;
}

.nav-left {
  display: flex;
  align-items: center;
}

.night-mode-switch {
  display: flex;
  margin-left: 1.5rem;
}

.nav-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}

.nav-item {
  transition: 0.25s;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 5rem;
  border-bottom: 4px solid transparent;
}

.nav-link {
  color: var(--color-text);
}

.nav-link:hover {
  color: var(--color-primary);
}

.active {
  border-bottom: 4px solid var(--color-primary);
}

.active a {
  color: var(--color-primary);
}

.nav-toggle {
  display: flex;
}

.nav-close {
  position: absolute;
  right: 1.5rem;
  top: 1.5rem;
  height: 1.5rem;
  fill: var(--color-text);
}

.nav-menu {
  height: 100%;
}

@media screen and (max-width: 768px) {
  .nav-menu {
    width: 100%;
    height: auto;
    height: initial;
    position: fixed;
    top: -100%;
    left: 0;
    padding: 3rem;
    box-shadow: 1px -5px 15px hsl(0deg 8% 40% / 50%);
    border-radius: 0 0 1.5rem 1.5rem;
    transition: 0.5s;
    background-color: var(--color-body);
    box-sizing: border-box;
  }

  .nav-item {
    margin: 1rem;
  }

  .header {
    border-radius: 0 0 1.5rem 1.5rem;
  }
}

@media screen and (min-width: 769px) {
  .nav-toggle,
  .nav-close {
    display: none;
  }

  .nav-list {
    flex-direction: row;
  }
}

.show-menu {
  top: 0;
}

.section {
  padding: 3rem;
  margin: auto;
  max-width: var(--width-desktop);
  min-height: 100vh;
  display: flex;
  justify-content: center;
}

.section-title {
  font-size: var(--font-size-3);
}

.section-subtitle {
  font-size: var(--font-size-0-75);
}

.home {
  display: flex;
  align-items: center;
  flex: 1 1;
}

.home a {
  display: inline-flex;
}

.home-title {
  font-size: 3rem;
}

.home-subtitle {
  font-size: var(--font-size-1-25);
  color: var(--color-text);
  margin-bottom: 0.75rem;
}

.home-description {
  text-align: justify;
  margin-bottom: var(--margin-2);
}

.home-blob {
  fill: var(--color-primary);
}

.home-content,
.home-image-container {
  flex: 1 1;
}

.home-image-container {
  background-color: var(--color-primary);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;

  -webkit-mask-image: url(/portfolio/static/media/home-blob.c2cd5be7.svg);

          mask-image: url(/portfolio/static/media/home-blob.c2cd5be7.svg);
  -webkit-mask-repeat: no-repeat;
          mask-repeat: no-repeat;
}

.home-img {
  max-width: 100%;
  left: var(--margin-1-5);
  position: relative;
}

.scroll-down {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: var(--color-text);
  transition: 0.4s;
  margin-top: 3.5rem;
  padding: 0 2rem;
}

.scroll-down:hover {
  transform: translateY(4px);
}

.scroll-down .icon-arrow-up {
  margin-left: 0.25rem;
  height: 1rem;
  transform: rotate(180deg);
  fill: var(--color-text);
}

.scroll-down-container {
  width: 1rem;
  height: 2rem;
  border-radius: 8px;
  border: 2px solid var(--color-text);
  position: relative;
  margin-right: 1rem;
}

.scroll-down-bubble {
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 50%;
  background-color: var(--color-text);
  position: absolute;
  left: 2px;
  animation: animation-move-up-down 2.5s ease-in-out infinite;
}

@media screen and (max-width: 768px) {
  .home {
    flex-direction: column-reverse;
    text-align: center;
  }

  .home button {
    margin: auto;
  }

  .scroll-down {
    display: none !important;
  }
}

@keyframes animation-move-up-down {
  0%,
  100% {
    bottom: 4px;
  }
  50% {
    bottom: 16px;
  }
}

.btn {
  display: flex;
  align-items: center;
  padding: 1rem 2rem;
  border-radius: 8px;
  border: none;
  font-size: var(--font-size-1-25);
  font-family: var(--body-font);
  font-weight: 600;
  box-shadow: hsla(var(--color-hue), 31%, 20%, 0.4) 0 2px 4px,
    hsla(var(--color-hue), 31%, 20%, 0.3) 0 7px 13px -3px,
    hsla(var(--color-hue), 6%, 28%, 0.5) 0 -3px 0 inset;
  cursor: pointer;
  transition: 0.4s;
}

.btn:hover {
  transform: translateY(-2px);
  opacity: 0.9;
}

.btn:disabled {
  cursor: not-allowed;
  background-color: var(--color-text-light);
}

.btn-primary {
  color: #fff;
  background-color: var(--color-primary);
}

.btn-primary .icon {
  fill: #fff;
  height: 1.5rem;
  margin-left: 0.5rem;
}

.btn-outline {
  border: none;
  color: var(--color-title);
  background-color: var(--color-container);
}

.btn-outline .icon {
  fill: var(--color-title);
  margin-left: 0.5rem;
}

.about {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 1 1;
}

.about-title {
  text-align: center;
}

.about-content {
  width: 100%;
  padding: 2rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  box-sizing: border-box;
}

.about-rounder {
  box-sizing: border-box;
  width: var(--font-size-1);
  height: var(--font-size-1);
  background-color: var(--color-primary);
  border-radius: 50%;
  border: 2px solid var(--color-primary-light);
  position: relative;
  left: -1.5rem;
  top: var(--font-size-1-25);
}

.about-line {
  position: absolute;
  left: -16px;
  top: 24px;
  width: 1px;
  height: 100%;
  background-color: var(--color-primary);
}

.about-content-container {
  padding: 1rem;
  margin: 1rem;
  position: relative;
  flex: 1 1;
  max-width: 300px;
  box-sizing: border-box;
}

.about-content-container-title {
  display: flex;
}

.about-content h3 {
  font-size: 1.25rem;
  color: var(--color-primary);
  margin: 0.75rem 0;
}

.about-content p {
  font-style: italic;
  font-size: var(--font-size-1);
  color: var(--color-text);
}

.icon-calendar {
  margin-right: var(--margin-0-5);
  width: var(--font-size-0-75);
  height: var(--font-size-0-75);
  fill: var(--color-primary);
}

.icon-education,
.icon-suitcase {
  margin-right: var(--margin-0-5);
  fill: var(--color-primary);
  width: var(--font-size-1-5);
  height: var(--font-size-1-5);
}

.form-field {
  margin-bottom: 1.5rem;
}

input,
textarea {
  resize: none;
  border: 1px solid transparent;
  box-sizing: border-box;
  outline: none;
  width: 100%;
  border-radius: 8px;
  padding: 1rem;
  margin: 0.25rem 0;
  font-family: var(--body-font);
  color: var(--color-title);
  background-color: var(--color-input);
}

input:-ms-input-placeholder, textarea:-ms-input-placeholder {
  color: var(--color-text-light);
}

input::placeholder,
textarea::placeholder {
  color: var(--color-text-light);
}

.form-field-label {
  align-items: baseline;
  display: inline-flex;
}

label {
  margin-right: 0.5rem;
  color: var(--color-text-light);
  text-transform: capitalize;
}

.input-error-field {
  border: 1px solid var(--color-primary);
}

.input-error-message {
  font-size: 0.825rem;
  color: var(--color-primary);
}

.contact-form-modal {
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 8px;
  padding: 0.5rem;
  transform: translate(-50%, -50%) !important;
  margin: auto auto;
  background-color: var(--color-container);
}

.contact-form-modal-content {
  padding: 1rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.contact-form-modal .icon-close {
  height: 1.75rem;
  float: right;
}

.icon-success {
  cursor: auto;
  cursor: initial;
  height: 5rem;
  fill: var(--color-primary);
}

@media screen and (max-width: 767px) {
  .contact-form-modal {
    width: 100%;
    height: 100%;
  }

  .icon-close {
    margin: 1rem 1rem 0 0;
  }
}

.contact-form {
  margin: var(--margin-1) 0;
  flex: 2 1;
}

.contact-form button > svg {
  fill: #fff !important;
  height: 1.25rem !important;
  animation: rotating 1s linear infinite;
}

.contact-form button > svg:hover {
  fill: #fff;
  cursor: none;
}

@media screen and (max-width: 768px) {
  .contact-form button {
    width: 100%;
    justify-content: center;
  }
}

@keyframes rotating {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}

.contact-links {
  margin: var(--margin-1) 0;
  flex: 1 1;
}

.contact-link {
  display: flex;
  align-items: center;
  margin-right: 1rem;
}

.contact-link-info {
  margin: var(--margin-0-5);
  flex: 1 1;
}

.contact-link-info h3 {
  color: var(--color-title);
  margin: 0.125rem 0;
}

.contact-link a {
  text-decoration: underline;
  color: var(--color-text);
}

.contact-link a:hover {
  color: var(--color-primary);
}

.contact {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 1 1;
}

.contact .icon {
  fill: var(--color-primary);
  height: 2.5rem;
  margin-right: var(--margin-1);
}

.contact-container {
  display: flex;
  flex-wrap: wrap;
}

@media screen and (max-width: 768px) {
  .contact-container {
    flex-direction: column;
  }

  .contact-form button {
    width: 100%;
    justify-content: center;
  }
}

.footer {
  display: flex;
  flex-direction: column;
  position: relative;
  text-align: center;
  color: var(--color-title);
  background-color: var(--color-primary-two);
}

.footer a,
.footer h3,
.footer p {
  color: hsla(0, 0%, 100%, 0.863);
}

.footer a:hover,
.footer-links svg:hover {
  fill: var(--color-input);
  color: var(--color-input);
}

.footer .nav-list {
  justify-content: center;
}

.footer-back-to-top {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--color-primary-two);
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: -1.5rem;
  transform: translateX(-50%);
}

.footer .icon-arrow-up {
  height: 70%;
  fill: hsla(0, 0%, 100%, 0.863);
  transition: 0.4s;
}

.footer .icon-arrow-up:hover {
  transform: translateY(-6px);
}

.footer-links {
  margin: 4rem 0 1rem;
  text-align: center;
}

.footer-links svg {
  height: 2rem;
  margin: 0 0.5rem;
  fill: hsla(0, 0%, 100%, 0.863);
}

.footer p {
  padding: 0.5rem 0;
  font-size: 0.75rem;
}

.footer-divider {
  position: relative;
}

hr {
  border: 0;
  height: 1px;
  width: 100%;
  position: relative;
  background-image: linear-gradient(
    to right,
    var(--color-primary-two),
    #fff,
    var(--color-primary-two)
  );
  background-color: #fff;
}

.center-diamond {
  position: absolute;
  width: 6px;
  height: 6px;
  background: #fff;
  top: 5px;
  left: 50%;
  transform: translate(-50%) rotate(-45deg);
}

.title {
  text-align: center;
  font-size: 3rem;
}

.subtitle {
  text-align: center;
  font-size: var(--font-size-1-25);
  color: var(--color-text);
}

.skills {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 1 1;
}

.skills-content {
  align-items: baseline;
  text-align: justify;
  display: grid;
  grid-gap: 3rem;
  gap: 3rem;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  margin: 1rem 0;
}

.skills-description {
  text-align: justify;
}

.skills-items {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
}

.skills-item {
  padding: 0.5rem;
}

.skills-item-badge {
  padding: 0.25rem 0.5rem;
  border-radius: 8px;
  border-bottom: 3px solid var(--color-primary);
  margin-right: 0.75rem;
  color: var(--color-title);
  background-color: var(--color-input);
  align-items: center;
  justify-content: center;
  display: flex;
  font-weight: 600;
  transition: 0.4s;
}

.skills-item-badge:hover {
  cursor: pointer;
  transform: translate(2px, -2px);
}

.skills-item-badges {
  grid-gap: 0.75rem;
  gap: 0.75rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
}

.skills-item-title {
  margin: 0 0 1rem;
  padding: 0.25rem 0;
  text-align: center;
  border-bottom: 1px solid var(--color-primary-light);
}

.download-cv-container {
  margin: var(--margin-2) 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  color: #fff;
  background-color: var(--color-primary);
  border-radius: 1rem;
  padding: 2rem 2rem 0;
}

.download-cv-container-title {
  color: #fff;
  margin: 1rem 0;
}

.download-cv-container button {
  margin: 2rem 0;
}

.download-cv-container img {
  max-width: 100%;
  max-height: 300px;
}

@media screen and (max-width: 1071px) {
  .download-cv-container button {
    margin: inherit auto;
    width: 100%;
    justify-content: center;
  }
}

